body {
  max-width: 707px;
  margin: auto;
  padding: 4vw;
  background: #eee8d5;
}

button {
  font-size: 13.5px;
  margin-left: -9px;
  background-color: inherit;
  border: none;
  color: blue;
  outline: none;
}

.RichEditor-root {
  font-family: "Georgia", serif;
  font-size: 14px;
  padding: 15px;
}

.RichEditor-editor {
  cursor: text;
  font-family: "Cabin", sans-serif;
  font-size: 18px;
  margin: 0 -15px -15px;
  padding: 15px;
  min-height: 100px;
}

.RichEditor-editor .RichEditor-blockquote {
  border-left: 2px solid grey;
  color: rgb(148, 148, 148);
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 10px;
  padding: 10px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
  background-color: rgba(0, 0, 0, 0.05);
  font-family: "Menlo" monospace;
  font-size: 16px;
  padding: 10px;
}

.RichEditor-controls {
  font-family: "Helvetica", sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.RichEditor-styleButton {
  color: #999;
  cursor: pointer;
  margin-right: 16px;
  padding: 2px 0;
  display: inline-block;
}

.RichEditor-activeButton {
  color: #34f;
}



